import { render } from '@testing-library/react'
import React from 'react'
import { tikets, filters } from '../utils/data'
import s from './Navbar.module.css'
const statickTikets = [...tikets]

export const Navbar = ({renderData}) => {
    const [checkFilters, setCheckFilters] = React.useState(
        new Array(filters.length).fill(false)
    )
    
    const filterByEvent = (event) => {
        let data = []
        switch (event) {
            case 'all':
                    data = [...statickTikets]
                break
            case 'nonstop':
                const filterByZero = statickTikets.filter( tiket => {
                    if(tiket.transfers.length === 0){
                        return tiket
                    }
                })
                data = [...filterByZero]
                break;
                case 'one':
                const filterByOne = statickTikets.filter( tiket => {
                    if(tiket.transfers.length === 1){
                        return tiket
                    }
                })
                data = [...filterByOne]
                break;
                case 'two':
                const filterByTwo = statickTikets.filter( tiket => {
                    if(tiket.transfers.length === 2){
                        return tiket
                    }
                })
                data = [...filterByTwo]
                break;
                case 'three':
                const filterByThree = statickTikets.filter( tiket => {
                    if(tiket.transfers.length === 3){
                        return tiket
                    }
                })
                data = [...filterByThree]
                break;
                
            default:
                break;
        }

        return data
    }

    const filterHander = (position) => {
        const updatedChecks = checkFilters.map( (item, ind) => {
            return position === ind ? !item : item 
        })
        
        setCheckFilters(updatedChecks)

        let resultData = []
        updatedChecks.forEach((item, ind) => {
            if(item === true){
                if(filters[ind].id === 'all'){
                    setCheckFilters(checkFilters.map(item => {
                        return item = true
                    }))
                }
                resultData = [...resultData, ...filterByEvent(filters[ind].id)]
            }

        })
        resultData = [...new Set(resultData)]
        const result = resultData.length === 0 ? [...statickTikets] : [...resultData]
        renderData(result)
    }

    return (
        <div className={s.navbar}>
            <div className={s.navbar__wrapper}>
                <label>
                <span className={s.title}> </span>
                </label>
                {
                    checkFilters.map( (item, ind) => {
                        return (
                            <label key={ind} className={s.form__checkbox}>
                            <input
                             type="checkbox"
                            id={s.check}
                            checked={item}
                            onChange={() => filterHander(ind)}
                            />
                            <span>{filters[ind].name}</span>    
                        </label>
                        )
                    })
                }
            </div>
        </div>
  )
}
